

.Blog ul {
    list-style: none;
    margin: 0;
    padding: 0;
    text-align: center;
}
.Blog li{
    display: inline-block;
    margin: 20px;

}

.Blog a{
    color: #000;
    text-decoration: none;
    font-weight: 400;
}
.Blog a:hover,
.Blog a:active,
.Blog a.active {
    color: #fa923f;
}